var comdic = require('./comdic');

let dicnum = 0;
const lang_arr = ['en','ja','ru'];
const set_cur_dic = (lang_name) => dicnum = lang_arr.findIndex(e => e == lang_name);
const cur_lang = () => lang_arr[dicnum];

function mass_replace(w, n, r) {
	w = w.toLowerCase();
	n = n.split(' ');
	r = r.split(' ');
	for (let i=0; i < r.length; i++) {
		w = w.split(r[i]).join(n[i]);
	}
	return w;
}

function transliterate_to_Nao(language, w) {

	if (language == 'Belarusian') {
		w = w.toLowerCase().replace(/(|)([]?[])/g, function(r) {
			let n;
			if (r[0] == '')
				n = '' + r.slice(r.length > 2 ? -2 : -1);
			else if (r[0] == '')
				n = '' + r.slice(r.length > 3 ? -2 : -1);

			return n;
		});

		let n = "tts ttc    "  + "ia io iu ii ie ii a b v g d e j z i i k l m n o p r s t u f h c y e  i ts tc u";
		let r = "     "    +    "     i                           '   ";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Bulgarian') {
		let n = "ia io iu a b v g d e j z i i k l m n o p r s t u f h c ct  ts tc y";
		let r = "                              ";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Chinese_pinyin') {
		w = w.split(' ').join('').toLowerCase().split('').join('').normalize('NFD').replace(/[\u0300-\u036f]/g, "").split('').join('');
		w = ' ' + w + ' ';

		let n = "s z c sh zh ch r x q j - -y -w s  t ts  ts u dz u i i dz y j c - Na Ni Nu Ne iou uen iu uei en n";
		let r = "si zi ci shi zhi chi ri xu qu ju yu y w x sh ch q zh c wu z w yi y j    ' na ni nu ne iu un  ui en N";
		w = mass_replace(w, n, r).split(' -').join(' ');
	}
	else if (language == 'Czech') {
		let n = "i i r  g h u ie   j ts c tc nia niu nio tia tiu tio dia diu dio n t d ks";
		let r = "j y  ch h       c   a u o a u o a u o    x";

		w = mass_replace(w, n, r).normalize('NFD').replace(/\u0301/g, "")
		.replace(/([^aiyueo])(r|l)([^aiyueo])/g,'$1y$2$3');
	}
	else if (language == 'Finnish') {
		let n = "i a o";
		let r = "j  ";
		w = mass_replace(w, n, r);
	}
	else if (language == 'German') {
		w = w.toLowerCase().replace(/([mns]){2}/,'$1');
		let n = "f v k c   i u e o  h z ts s aj oj i i iu io";
		let r = "v w ck sch ch ha hi hu he ho h  tz z  ei eu ie j  ";

		w = mass_replace(w, n, r).replace(/s([pt])([aiueo])/, 'c$1$2');
	}
	else if (language == 'Italian') {
		w = w.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '').replace(/([bcdfglmnpqrstvz])\1/g,'$1').replace(/(sc|c|g)i([aueo])/g,'$1$2');
		let n =     "c c i ti ji  t j e te je k c ts  ni li";
		let r = "q cq sci ci gi sc c g sce ce ge c  z h gn gli";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Japanese_hepburn') {
		w = w.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '');
		let n = "tc i u c";
		let r = "ch y w sh";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Malay') {
		let n = "i u tc";
		let r = "y w c";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Polish') {
		let n = "i v u u n ji j z en on i  i    j p k ty j h ts c tc";
		let r = "j w    dzi d    si  ci  cz sz  prz krz trz rz ch c  ";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Russian') {
		let n = "      ci    " + " ia io iu ii ie ii a b v g d e j z i i k l m n o p r s t u f h c y e  - ts tc";
		let r = "          "    +  "      i                             ";
		w = mass_replace(w, n, r)
	}
	else if (language == 'Slovak') {
		w = w.normalize('NFD').replace(/\u0301/g, "").normalize('NFC');
		let n = "a i i r  g h u   j ts c tc lia liu lio nia niu nio tia tiu tio dia diu dio l n t d ks";
		let r = " j y  ch h      c   a u o a u o a u o a u o     x";

		w = mass_replace(w, n, r).replace(/([^aiyueo])(r|l)([^aiyueo])/g,'$1y$2$3');
	}
	else if (language == 'Spanish') {
		let n = "a i u e o  zi ze s k ji je i b u ni  h k ge gi tc i r";
		let r = "     ch ci ce z c gi ge y v   h j qu gue gui  ll rr";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Turkish') {
		w = w.replace(/I/g,'');
		let n = "i y o u j tc c";
		let r = "y    c  ";

		w = mass_replace(w, n, r).normalize('NFD').replace(/\u0302/g, "").normalize('NFC')
		.replace(/([aiueoy])([aiueoy])/, '$1$2')
		.replace(/([aiueoy])/, '$1$1');
	}
	else if (language == 'Ukrainian') {
		let n = "ia io iu ii ie ii a b v g d e j z y i i i k l m n o p r s t u f h c ctc  ts tc";
		let r = "     i                             ";
		w = mass_replace(w, n, r);
	}
	else if (language == 'Vietnamese') {
		w = w.toLowerCase().normalize('NFD').replace(/^(.*)([\u0300-\u036f])([\u0302\u0306])(.*)$/g, "$1$3$2$4");
		w = w.replace(/e/,'').replace(/o/,'').replace(/a/,'').replace(/a/,'').replace(/u/,'').replace(/o/,'').replace(/[\u0300-\u036f]/g,'');
		w = ' ' + w + ' ';

		let g = "y_i__a__u__y__y__o__e_d_z__d_tr_j_ach_aik_anh_aing_ech_ek_enh_eng_inh_ing_ich_ik_ch__nhi_ni_nh_ni_ph_f_gi _zi _gi_z_c_k_q_k_ghi_gi_ghe_ge__tc_s_c_x_s";

		g = g.split('_');

		for (let i=0; i < g.length/2; i++) {
			w = w.split(g[i*2]).join(g[i*2+1]);
		}

		w = w.trim().replace(/[ \-]/g,'');
	}
	else return null;

	return "'" + w.trim().split(' ').join('-');
}

const transliteration_languages = ['Belarusian', 'Bulgarian', 'Czech', 'Finnish', 'French', 'German', 'Italian', 'Japanese',
'Malay', 'Mandarin', 'Polish', 'Russian', 'Slovak', 'Spanish', 'Turkish', 'Ukrainian', 'Vietnamese'];

const get_comdic = comdic.get_comdic;

const is_an_hsl_color = (d) => {
	if (d[0] != 'ha' || d.length < 2) return false;

	let t = translate_elem(d[1]).translation;

	if (isNaN(t)) {
		if (d[1] != 'ga') return false;
		else if (d[2] == 'bul') return false;
	}
	if (t > 5) return false;

	let z;
	for (let i=1; i<d.length; i++) {
		z = translate_elem(d[i]).translation;
		if ( isNaN(z) && !['lai','lau','bul','bil','ga','pu','pi'].includes(d[i])) return false;
	}

	if ( ['pu','pi','ga'].includes(d[d.length-1]) ) return false;

	return true;
};

