const to_Polish_writing = (d) => {
	let w = '  ';

	for (let e of d) {
		e = e.split('tc').join('').split('ts').join('').split('dz').join('').split('-').join(' ');
		if (e.startsWith("'")  ) {
			let new_di = "";
			let k=1;
			while(!is_a_vowel(e[k]) && k < e.length ) { new_di += e[k]; k++; } 
			for (; k < e.length; k++) {
				if ( is_a_vowel(e[k+1]) && "pbtdkgfvszjhlmnrwc".includes(e[k]) ) new_di += ' ';
				new_di += e[k];
			}
			w=w.concat(new_di+' ');
		}
		else
			w=w.concat(e+' ');
	}
	w += ' ';

	let g = "ee_e_oo_o_jy_dy_ num _ noom _dia_diia_bol_boll_ nia _ niaa _ sa _ saa _ zi _ zee _'_ _ong__ng _m _ si _ see _g _gg _b _bb _d _dd _ji_dzi_j _d _j_dzi_ci_i_c_sz_h _ch _aui_ai_ia_ja_hja_chja_ai_aj_ei_ej_oi_oj_ui_uj_yi_yj_au_a_yu_y_uo_o_ua_a_ae_aae_dzj_dzi_ue_e_ou_o_oe_o'e_eu_e_naj_naaj_dzi ye_dzie_kis_kiss_indd_indy_sea_sse'ea_hea_he'aa_tio_ttio_tie_tje_un_uun_kon_konn_turk_turk_pio_pjo_fas_faus_ ia_ ja_ ie_ ie_ io_ jo_ iu_ ju_gin_ginn___ja_cja_je_cje_iu_cju_io_cjo_o _co __tz_j_ij__u_sz__dzis_dziss_in_inn_hie_hje_dzioj_dzioi_moj_moi_sit_sitt_y _y";

	g = g.split('_');

	for (let i=0; i < g.length/2; i++) {
		w = w.replaceAll(g[i*2], g[i*2+1] );
	}

	//w = w.trim();
	//w = w.charAt(0).toUpperCase() + w.slice(1) + '.';
	
	return w.trim();
};


function pronounce_ukrainian(d) {
	let w = " " + d.join(" ") + " ";

	let transformations = {
		" go ": " goo ",
		ai:"", oi: "", ui:"", ei: "", tc:"", ts:"", ng:"",
		p:"", t:"", k:"", b:"", d:"", g:"", l:"", m:"", n:"", h:"", f:"", s:"", r:"", j:"", z:"", v:"", c:"",
		a:"", i:"", u:"", e:"", o:"", y:""
	};

	for (let t in transformations) {
		w = w.replaceAll(t, transformations[t]);
	}

	w = w.trim();
	w = w.charAt(0).toUpperCase() + w.slice(1) + '.';

	responsiveVoice.speak(w, "Ukrainian Female", {rate: 0.7, pitch: 1.1} );
}


const is_a_vowel = (c) => "aiueoy".includes(c);

const is_a_consonant = (c) => "pbtdkgfvszjhlmnrwc".includes(c);

const includes_a_vowel = (w) => {
	for (let e of w)
		if (is_a_vowel(e)) return true;
	return false;
};

const preprocess = (foo, w) => {
	let f = get_first_word(w);
	let r = w.slice(f.length);

	let k = f.split("/");
	f = foo(k[0]);

	for (let i=1; i<k.length; i++) {
		f += "/" + foo(k[i]);
	}

	return f+r;
};


const add_ing = (w) => add_ing_or_ed(w, "ing");
const add_ed = (w) => add_ing_or_ed(w, "ed");

const add_ing_or_ed = (w, ing_or_ed) => {
	let f = get_first_word(w);
	let r = w.slice(f.length);

	if (
	       f.length == 2 
	   	|| f.endsWith('w')
	   	|| ( f.endsWith('y') || f.endsWith('ee') )
			 &&
			ing_or_ed == "ing"
		) {
		//do nothing
	}
	else if (f.endsWith('y') && is_a_consonant( f[f.length - 2] )) {
		f = f.slice(0,-1) + 'i';
	}
	else if (f.endsWith('ie') && ing_or_ed == "ing") {
		f = f.slice(0,-2) + 'y';
	}
	else if (f.endsWith('e')) {
		f = f.slice(0,-1);
	}
	else {
		let last_three_characters = f.slice(-3);

		if (
		is_a_consonant(last_three_characters[0]) &&
		    is_a_vowel(last_three_characters[1]) &&
		is_a_consonant(last_three_characters[2]) 
		) {
			if (   !includes_a_vowel(f.slice(0,-3))    ) 
				f += last_three_characters[2];
			else if ( ["begin","refer","control"].includes(f) ) {
				f += last_three_characters[2];
			}
		}


	}

	w = f + ing_or_ed + r;
	return w;
};

const add_s = (w) => {
	let f = get_first_word(w);
	let r = w.slice(f.length);

	if (f == "be") f = 'i';
	else if (f == "have") f = 'ha';
	else if (f.match(/(o|s|ch|sh|x)$/) ) f += "e";
	else if (f.match(/[^aiueo]y$/) ) f = f.substr(0,f.length-1) + 'ie';

	w = f + 's' + r;
	return w;
};

const get_V3 = (w) => {
	let f = get_first_word(w);
	let r = w.slice(f.length);

	let n = irregular_verbs[f];

	return n ? n[1] + r : add_ed(f) + r  ;
};

const get_V2 = (w) => {
	let f = get_first_word(w);
	let r = w.slice(f.length);

	let n = irregular_verbs[f];

	return n ? n[0] + r : add_ed(f) + r ;
};

const get_passive = (w) => "be " + preprocess(get_V3,w);
const get_continuous = (w) => "be " + preprocess(add_ing,w);

const get_first_word = (str) =>
	str.indexOf(' ') === -1 ? str : str.substr(0, str.indexOf(' '));

const is_a_consonant_rus = (c) => ''.includes(c);

const create_short_forms = (stem) => {
	//let stem = w.slice(0,-2);
	let r = [];
	if (stem.endsWith('') ) stem = stem.slice(0,-1);
	if (is_a_consonant_rus(stem[stem.length-1]) && is_a_consonant_rus(stem[stem.length-2])  ) r.push(stem.slice(0,-1)+''+stem.slice(-1));
	r.push(stem);
	return r;
};


const create_equivalents_rus = (w) => {
	w = w.replaceAll('','');
	let r = [w];
	let f;

	if (w.startsWith(' ')) {
		let f = get_first_word(w.slice(5));
		
		if (f.endsWith('') || f.endsWith('')) {
			let rest = w.slice(5).slice(f.length);
			let stem = f.slice(0,-2);
	
			r = [w, f.slice(0,-1)+''+rest, f.slice(0,-2)+''+rest];
			r = r.concat(create_short_forms(f).map(e => e+rest) );
		}
	}
	else if ( f = w.match(/^(.+)(|||)$/) ) {
		r = r.concat(create_short_forms( f[1] ) );

		r.push( ' ' + f[1] +  '', ' ' + f[1] +  '');
	}
	else if (is_a_consonant_rus(w.slice(-1) )  ) {
		let y = w;
		if (w.endsWith('') || w.endsWith('') ) y+= '';

		if ( ''.includes(w[w.length-2])  ) {
			w = w.slice(0,-2)+w.slice(-1);
			r = r.concat([w+'', w+'', w+'']);
		}
		r = r.concat([y+'',y+'',y+'']);

		r.push( ' ' + w +  '', ' ' + w +  '');
	}

	return r;
};

const create_equivalents_eng = (w) => {
	let r = [w];

	let f;
	function toggle(w, a, b) { return w == a ? b : a;}

	for (let e of r)
		if (f = e.match(/^(.+[iy])(s)(e|ed|ation|ing|er|able)$/)) r = union(r, [f[1] + toggle(f[2], 's', 'z' ) + f[3]]  );
	
	for (let e of r)
		if (f = e.replace(/..our/, 'or')) r = union(r, [f] );

	for (let e of r)
		if (f = e.replace(/ae|oe/, 'e')) r = union(r, [f] );

	for (let e of r)
		if (f = e.replace(/re$/, 'er')) r = union(r, [f] );

	for (let e of r)
		if (f = e.replace(/ogue$/, 'og')) r = union(r, [f] );

	for (let e of r) {
		if (e.startsWith('be ')  ) r = union(r, [e.slice(3)]);
		else r = union(r, ['be '+e]);
	}

	return r;
};

const create_equivalents_jap = (w) => {
	let r = [w];
	
	return r;
}

