function get_all_words_that_(cond, f) {
	cond = {
		'start': (a,b) => a.startsWith(b),
		'end': (a,b) => a.endsWith(b),
		'contain': (a,b) => a.includes(b)
	}[cond];

	let res = [];
	let d = disassemble(f);
	if (!d) return null;
	d = '-' + d.join("-") + '-';

	for (let w in cur_dic().from_Nao) {
		if ( cond(w, f) ) {
			if (  cond(('-' + disassemble(w).join("-") + '-'), d)   )
				res.push(w);
		}
	}
	return res;
}

function get_ending(word) {
	let res = word.match(/[aiueo]?[aiueo][pbtdkgfvszjhlmnrwc]*$/);
	return res ? res[0] : null;
}

function get_rhymes(word) {
	let res = [];
	let ending = get_ending(word);
	if (ending === null) return res;

	for (let w in cur_dic().from_Nao) {
		let e = get_ending(w);
		if (e == ending) res.push(w);
	}

	return res.filter(x => x != word).sort( (a,b) => a.length > b.length ? 1 : a.length < b.length ? -1 : 0 );
}

function is_a_dirty_word(d) {
    let s = '-'+d.join('-')+'-';
    for (let w of ['fe','hi','kas','gun','bu-mi','bu-mu', 'bu-mil', 'go-dot-cun']) {
        if (s.includes('-'+w+'-')) return true;
    }
    if (d.length == 2 && d[0] == 'mon' || d[0] == 'tam') return true;
    return false;
}

function disassemble_deep_recursive(d, split_please) {
	let res = [];
	let arr = smart_translate2(d, split_please);

	for (let w of arr) {
		let o = tr_from_Nao(w) || {};
		//console.log({o,w});
		o.id = o.nao + Math.random();
		let dn = disassemble(w);
		let d2 = stick_digits_together(dn);

		if (d2.length == 1 && dn.length > 1 || is_an_hsl_color(dn) ) o.children = dn.map(function(e){
			let o2 = tr_from_Nao(e);
			o2.id = o2.nao + Math.random();
			return o2;
		});
		else if (d2.length > 1) o.children = disassemble_deep_recursive(d2, true);

		res.push(o);
	}
	return res;
}

function disassemble_deep(w) {
	let d = stick_digits_together(disassemble(w));
	return disassemble_deep_recursive(d, false);
}


const Nao_color_to_HSL = (d) => {
	let hue = "";
	let saturation = 100;
	let luminosity = 50;

	let i;
	let z;

	for (i=1; i < d.length; i++) {
		z = translate_elem(d[i]).translation;
		if ( isNaN(z) ) break;
		hue += z;
	}

	hue = hue / Math.pow(10,hue.length-1) * 60;

	if (i < d.length ) {
		if ( d[i] == 'ga') {
			if (d[i+1]== 'lai') luminosity = 100 ;
			else if (d[i+1]== 'lau') luminosity = 0;
		}
		else if ( d[i].match(/^lai|lau$/) ) {
			let s = d[i] == 'lai' ? 1 : -1;
			luminosity = "";

			for (i=i+1; i < d.length; i++) {
				z = translate_elem(d[i]).translation;
				if ( isNaN(z) ) break;
				luminosity += z;
			}

			if ( luminosity === '') luminosity = 50+25*s;
			else luminosity = 50 + luminosity / Math.pow(10,luminosity.length)*100/2*s;
		}
	}

	if (i < d.length ) {
		if ( d[i] == 'ga') {
			if (d[i+1] == 'bul') saturation = 100;
			else if (d[i+1] == 'bil') saturation = 0;
		}
		else if ( d[i].match(/^bil|bul$/)  ) {
			let s = d[i] == 'bil' ? -1 : 1;
			saturation = "";

			for (i=i+1; i < d.length; i++) {
				z = translate_elem(d[i]).translation;
				if ( isNaN(z) ) break;
				saturation += z;
			}

			if ( saturation === '') saturation = 50+25*s;
			else saturation = 50 + saturation / Math.pow(10,saturation.length)*100/2*s;
		}
	}

	return 'hsl(' + hue + ',' + saturation + '%,' + luminosity + '%)';
};


function pad(x, n) {
	x += '';
	while(x.length < n) x = '0' + x;
	return x;
}

function HSL_to_Nao(hue, lum, sat) {
	let res = 'ha' + pad(hue,2);
	if (lum) res += ' ' + (lum >= 50 ? 'lai':'lau') + Math.abs(lum/5-10);
	if (sat) res += ' ' + (lum >= 50 ? 'bul':'bil') + Math.abs(sat/5-10);
	return res;
}



function binary_search(items, value){
    var firstIndex  = 0,
        lastIndex   = items.length - 1,
        middleIndex = Math.floor((lastIndex + firstIndex)/2);

    while (items[middleIndex] != value && firstIndex < lastIndex) {
    	if (value < items[middleIndex]) lastIndex = middleIndex - 1;
    	else if (value > items[middleIndex]) firstIndex = middleIndex + 1;
        middleIndex = Math.floor((lastIndex + firstIndex)/2);
    }

	return (items[middleIndex] != value) ? middleIndex+1 : middleIndex;
}

