function make_opposite(d) {
	let res = d.slice(0);
	for (let i=res.length-1; i >= 0; i--) {
		let opp_maybe = res[i].replace(/(i|u)[pbtdkgfvszjhlmnrwc]?$/, function(final) {
			let v = final[0] == 'i' ? 'u' : 'i';
			return v + final.slice(1);
		});
		if (opp_maybe != res[i] && tr_from_Nao(opp_maybe) && !['ris','rus','bis','bus','mais','maus'].includes(opp_maybe) ) {
			res[i] = opp_maybe; 
			return res;
		}
	}
	return null;
}

let words_by_length;

function random_element(array) {
	return array[Math.floor(Math.random()*array.length)];
}

function random_integer_between(a, b) {
	return Math.floor(Math.random()*(b-a+1) ) + a;
}

function random_word_with_length(word_length) {
	let words = words_by_length[ random_integer_between(word_length[0], word_length[1] ) ];
	return random_element(words);
}

function get_words_by_length() {
	if (words_by_length) return words_by_length;

	words_by_length = { 1:[], 2:[], 3:[], 4:[], 5:[], 6:[] };

	for (let w of Object.keys(cur_dic().from_Nao) ) {
		let d = disassemble(w);
		if (!d || is_a_dirty_word(d)) continue;
		let n = d.length;
		if (n > 6) n = 6;
		words_by_length[ n ].push(d);
	}

	return words_by_length;
}

