let elements;

function get_elements_cards() {
	if (elements) return elements;
	elements = [];

	for (let w in GGG) {
		let attrs = GGG[w][1].split(' ');
		let opp = make_opposite(disassemble(w));
		if (!attrs.includes("c") && opp) attrs.push("opp");

		if (! (attrs.includes("c") || opp )) continue;
		//console.log( );

		let main_tr = GGG[w][0].replace(/(.+) \((.+)\)/, '$1 $2').replace(' ...ing', '');
		if (GGG[w][0] != main_tr) attrs.push('tr');

		if (attrs.includes('v')) main_tr = preprocess(add_s, main_tr);

		let part_of_speech = '';
		if (attrs.includes('n')) part_of_speech = 'noun';
		else if (attrs.includes('v') || attrs.includes('adj')) part_of_speech = 'verb';
		else if (attrs.includes('prefix')) part_of_speech = 'prefix';
		else if (attrs.includes('prep')) part_of_speech = 'preposition';
		else if (attrs.includes('number')) part_of_speech = 'number';
		else if (attrs.includes('marker')) part_of_speech = 'marker';

		elements.push({nao:w, main_tr, attrs, part_of_speech });
	}
	//elements = elements.filter(e => e.attrs.includes("c") );
	return elements;
}


