<template><div class="d-flex justify-center">

<div class="b_naoword i_english" style="max-width: 600px">
	<component :lang="$route.params.lang" class="px-3" :is="Origins || $g.NotTranslated"></component>
</div>

</div></template>

<script>

import i18n from '@/i18n'
import Vue from 'vue'

export default {
	components: {},
	data() {
		return {

		}
	},
	methods: {

	},
	created() {

	},
	computed: {
		Origins() {
			return i18n.choose[this.$store.state.language].components.Origins || i18n.choose['en'].components.Origins;
		},
		have_lang() {
			return !!this.$route.params.lang;
		}
	}
}
</script>
