import { mdiArrowExpandRight, mdiArrowLeft, mdiArrowRight, mdiChat, mdiCheck, mdiChevronDown, mdiClose, mdiCog, mdiCommentQuote, mdiDiscord, mdiFileMultiple, mdiForum, mdiMagnify, mdiPatreon, mdiPlus, mdiReddit, mdiSchool, mdiSend, mdiStar, mdiTableSplitCell, mdiTelegram, mdiTranslate, mdiVolumeHigh } from '@mdi/js';

let mdi = { mdiArrowExpandRight, mdiArrowLeft, mdiArrowRight, mdiChat, mdiCheck, mdiChevronDown, mdiClose, mdiCog, mdiCommentQuote, mdiDiscord, mdiFileMultiple, mdiForum, mdiMagnify, mdiPatreon, mdiPlus, mdiReddit, mdiSchool, mdiSend, mdiStar, mdiTableSplitCell, mdiTelegram, mdiTranslate, mdiVolumeHigh };
 
Vue.prototype.$mdi = {};

for (let icon_name in mdi) {
	Vue.prototype.$mdi[icon_name.slice(3)] = mdi[icon_name];
	delete mdi[icon_name];
}

